/*
 * @Descripttion: js
 * @version: 0.1
 * @Author: ZJM
 * @Date: 2020-05-21 15:26:48
 * @LastEditors: ZJM
 * @LastEditTime: 2021-09-07 14:34:38
 */
// 
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
// 
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// ztree
import ZTree from '@/components/common/ZTree/ZTree';
// table
import VXETable from '@/components/common/Table';
// 
import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
// 
import { materialTypeGetTreeFunc } from '@/utils/publicFunction';
import { STATUS_CODE, TITLE, MSG, BTN_TEXT, CONSTANTS_MATERIAL, VARIABLE, EXPORT_NAME } from '@/utils/constantsFile';
// 
// import moment from 'moment';
// 
import addMaterial from '@/views/main/MaterialManagement/addMaterial.vue';
import materialTypeConfig from '@/views/main/MaterialManagement/materialTypeConfig.vue';
import SearchBox from '@/components/common/SearchBox';
import CustomColumn from '@/components/common/CustomColumn';
import ImportDialog from '@/components/common/ImportDialog';
// 
import {
  materialsGetListMaterialsQueryParameterAction,
  materialsUpdateByIdAction,
  materialsDeleteByIdsAction,
  materialsTypeGetTreeAction,
  materialsGetPageMaterialsByMaterialsTypeIdAction,
  materialsGetPageMaterialsByConditionAction,
  basicInformationGetListByBasicTypeAction
} from '@/api/material';
// import {
//   // extendPropertyGetListAction
// } from '@/api/extendConfig';
import {
  exportExportMaterialsTemplateAction
} from '@/api/export';
import {
  exportImportMaterialsAction
} from '@/api/import';
export default {
  name: 'MaterialManagement',
  components: {
    // 
    HeaderTitle,
    // 
    HeadOperationItem,
    // table
    VXETable,
    // 
    ZTree,
    // 
    addMaterial,
    // 
    materialTypeConfig,
    SearchBox,
    CustomColumn,
    ImportDialog
  },
  data() {
    return {
      EXPORT_NAME: EXPORT_NAME,
      VARIABLE: VARIABLE,
      BTN_TEXT: BTN_TEXT,
      CONSTANTS_MATERIAL: CONSTANTS_MATERIAL,
      // 
      deviceQRcode: '',
      deviceName: '',
      // 
      formUpdate: {},
      // 
      typeOptions: [],
      typeOptionsY: [],
      // 
      groupOptions: [],
      // 
      rulesUpdate: {
        materialsCode: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 20, message: '20' }
        ],
        materialsName: [
          { required: true, message: MSG.PLEASE_ENTER_CONTENT },
          { max: 20, message: '20' }
        ],
        materialsModel: [
          { max: 50, message: '50' }
        ],
        materialsTypeId: [{ required: true, message: MSG.PLEASE_CHOOSE_CONTENT }]
      },
      // 
      HeadTitleUrl: '',
      // table
      HeadTitleName: CONSTANTS_MATERIAL.MATERIAL_LIST,
      // / ids
      searchIdArr: [],
      // table
      deviceChecks: [],
      tableName: 'device',
      // 
      radio: 1,
      // --
      radioType: 1,
      // 
      radioTypeDisabled: [1, 2, 3],
      // tab
      tabPosition: 'group',
      // 
      showModelItem: false,
      // 
      fatherNodeData: [],
      //  0 1
      showPage: 0,
      // 
      modelOptions: [],
      materialsUnitList: [],
      materialsAttributes: [],
      // 
      formParams: {
        model: null
      },
      // 
      formInline: {
        cnName: '',
        shortName: '',
        parameterName: '',
        isWarning: 'null'
      },
      // 
      rules: {
        name: [{ required: true, message: MSG.PLEASE_ENTER_CONTENT, trigger: 'blur' }]
      },
      //  1  2 3
      operaType: 1,
      // label
      formLabelWidth: '150px',
      // label
      formLabelWidthShort: '50px',
      // 
      rightMenu: [],
      // 
      form: {
        name: ''
      },
      // 
      titlename: '',
      dialogVisibleQrCode: false,
      // 
      dialogVisibleUpdate: false,
      // 
      dialogVisible: false,
      // ztree
      ztreeObj: {},
      // ztree
      ztreeSelectedNode: {
        id: '-1',
        pId: '-1'
      },
      // ztree
      nodeData: [],
      // table
      tableLoading: false,
      // table
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true,
        slots: {
          // 
          left: 'pager_left'
        }
      },
      // table
      tableDatas: [],
      // table
      tableColumns: [],
      // 
      tableColumns_old: [
        {
          type: 'checkbox',
          width: '50',
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'materialsCode',
          title: CONSTANTS_MATERIAL.MATERIAL_CODE,
          width: 'auto',
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'materialsName',
          title: CONSTANTS_MATERIAL.MATERIAL_NAME,
          width: 'auto',
          fixed: 'left',
          align: 'center'
        },
        {
          field: 'materialsModel',
          title: CONSTANTS_MATERIAL.MATERIAL_MODEL,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'materialsAttributesName',
          title: CONSTANTS_MATERIAL.MATERIAL_ATTR,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'materialsTypeName',
          title: CONSTANTS_MATERIAL.MATERIAL_TYPE,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'materialsUnit',
          title: CONSTANTS_MATERIAL.MATERIAL_UNIT,
          width: 'auto',
          align: 'center'
        },
        {
          field: 'OPERATION',
          title: BTN_TEXT.OPERATION,
          width: '120',
          fixed: 'right',
          align: 'center',
          slots: {
            default: ({ row, column }) => {
              return [
                <el-dropdown
                  placement="bottom"
                  size="small"
                  onCommand={(command) => this.handleCommand(command, row)}
                >
                  <span class="operation-column" >
                    {BTN_TEXT.OPERATION}
                  </span>
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item class="operation-column" command="info">{BTN_TEXT.INFO}</el-dropdown-item>
                    <el-dropdown-item class="operation-column" command="delete">{BTN_TEXT.DELETE}</el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
                // <el-button onClick={() => this.toInfo(row)} type="text">
                //   {BTN_TEXT.INFO}
                // </el-button>,
                // <el-button onClick={() => this.setDelete(row)} type="text">
                //   {BTN_TEXT.DELETE}
                // </el-button>,
                // <el-button onClick={() => this.setQrCode(row)} type="text">
                //   {CONSTANTS_DEVICE_CONFIGURATION.PRINT_QRCODE}
                // </el-button>
              ];
            },
            header: ({ column }) => {
              return [
                <span>
                  <span>{column.title}</span>
                  <i onClick={() => this.showCustomColumn()} class="el-icon-s-tools set-btn"></i>
                </span>
              ];
            }
          }
        }
      ],
      // 
      tableColumns_head: [],
      // 
      tableColumns_operation: [],
      // 
      extendPropertyVoList: [],
      // 
      searchData: [],
      // 
      srearchList: [],
      // 
      isSearchBoxShow: false,
      // 
      dialogVisibleCustomColumn: false,
      isImportDialogShow: false
    };
  },
  computed: {},
  created() {
    // 
    this.GetListQueryParameter();
    // 
    this.getTree();
    // 
    this.setTableColume();
    // 
    this.findList();
  },
  watch: {
    // 
    tabPosition() {
      // 
      this.form = {
        name: ''
      };
      // ztree
      this.ztreeSelectedNode = {
        id: '-1',
        pId: '-1'
      };

      this.getTree();
    },
    // 
    dialogVisibleUpdate(val) {
      if (val === false) {
        this.$refs.updateForm.resetFields();
      }
    },
    // 
    dialogVisibleQrCode(val) {
      if (val === false) {
        this.deviceQRcode = '';
      }
    }
  },
  methods: {
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    handleCommand(command, row) {
      switch (command) {
        case 'info':
          this.toInfo(row);
          break;
        case 'delete':
          this.setDelete(row);
          break;
        case 'qrcode':
          this.setQrCode(row);
          break;
        default:
          break;
      }
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    deteleall() {
      if (this.deviceChecks.length === 0) {
        this.$message.error(MSG.PLEASE_CHOOSE_DELETE);
      } else {
        var arrdelete = [];
        this.deviceChecks.map(item => arrdelete.push(item.materialsId));
        this.$confirm(MSG.CONFIRM_CHOOSE_DELETE, TITLE.PROMPT, {
          confirmButtonText: BTN_TEXT.CONFIRM,
          cancelButtonText: BTN_TEXT.CANCEL,
          type: 'warning'
        })
          .then(() => {
            this.setDeleteChecks(arrdelete);
          })
          .catch(() => {
          });
      }
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    async setDeleteChecks(param) {
      try {
        const res = await materialsDeleteByIdsAction({
          ids: param
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          showSuccessMessage(res.message);
          this.findList();
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param data // 
     * @return: 
     * @author: ZJM
     */
    deviceTableChooseCheck(data) {
      this.deviceChecks = data;
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    submit() {
      if (this.tabPosition === 'group') {
        // 
        this.$refs.groupForm.validate(valid => {
          // 
          if (valid) {
            // 
            switch (this.radio) {
              case 1: // 
                this.addBrotherNode_Group();
                break;
              case 2: // 
                this.addChildNode_Group();
                break;
              case 3: // 
                this.renameCurrentNode_Group();
                break;
            }
          }
        });
      } else {
        this.$refs.groupForm.validate(valid => {
          // 
          if (valid) {
            // 
            switch (this.radio) {
              case 1: // 
                this.addBrotherNode_Type();
                break;
              case 2: // 
                this.addChildNode_Type();
                break;
              case 3: // 
                this.renameCurrentNode_Type();
                break;
            }
          }
        });
      }
    },
    /**
     * @description: 
     * @param val 
     * @return: 
     * @author: ZJM
     */
    showDialog(val) {
      // 
      this.operaType = val;
      // 
      this.dialogVisible = true;
      // 
      if (val === 3) {
        // 
        this.getTypeData(this.ztreeSelectedNode.id);
        this.titlename = BTN_TEXT.MODIFY;
        this.showModelItem = false;
      } else if (val === 1) {
        this.titlename = BTN_TEXT.ADD_BORTHER;
        // 
        if (this.ztreeSelectedNode.pId > 0) {
          this.showModelItem = true;
        }
      } else if (val === 2) {
        this.titlename = BTN_TEXT.ADD_CHILDREN;
        // 
        this.showModelItem = true;
      }
    },
    /**
     * @description: ztree
     * @param event 
     * @param nodes 
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeOnClick(event, nodes, obj) {
      // nameid
      this.searchIdArr = [];
      var nameUrl = [];
      nameUrl.push(nodes.name);
      var node = nodes.getParentNode();
      while (node) {
        nameUrl.push(node.name);
        node = node.getParentNode();
      }
      this.HeadTitleUrl = nameUrl.reverse().join(' / ');
      this.searchIdArr = [nodes.id];

      // 
      this.ztreeSelectedNode = nodes;
      // ztree
      this.ztreeObj = obj;
      // 
      this.tablePage.currentPage = 1;
      // 
      this.findList();
    },
    /**
     * @description: 
     * @param row 
     * @return: 
     * @author: ZJM
     */
    getChildren(ids, treeNode) {
      ids.push(treeNode.id);
      if (treeNode.isParent) {
        for (var obj in treeNode.children) {
          this.getChildren(ids, treeNode.children[obj]);
        }
      }
      return ids;
    },
    /**
     * @description: ztree
     * @param obj ztree
     * @return: 
     * @author: ZJM
     */
    ztreeHandleCreated(obj) {
      // ztree
      this.ztreeObj = obj;
      if (this.ztreeSelectedNode.id && this.ztreeSelectedNode.id !== '-1') {
        var nodes = this.ztreeObj.getNodesByParam(
          'id',
          this.ztreeSelectedNode.id,
          null
        );
        this.ztreeSelectedNode = nodes[0];
        this.ztreeObj.selectNode(nodes[0]);
        this.ztreeObj.expandNode(nodes[0], true);
      }
      this.findList();
    },
    /**
     * @description: table
     * @param currentPage 
     * @param pageSize 
     * @return: 
     * @author: ZJM
     */
    onTablePageChange(currentPage, pageSize) {
      // table
      this.tablePage.currentPage = currentPage;
      // table
      this.tablePage.pageSize = pageSize;
      // 
      this.findList();
    },
    /**
     * @description: 
     * @param val
     * @return: 
     * @author: ZJM
     */
    async findList() {
      try {
        // table
        this.tableLoading = true;

        const params = {
          current: this.tablePage.currentPage,
          size: this.tablePage.pageSize
        };
        const paramMore = {};
        if (this.isSearchBoxShow === false) {
          if (this.searchIdArr.length > 0) {
            params.materialsTypeId = this.searchIdArr[0];
          } else {
            params.materialsTypeId = -1;
          }
          const res = await materialsGetPageMaterialsByMaterialsTypeIdAction(params);
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 
            this.tableDatas = res.rows;
            this.tablePage.total = res.total;
            this.tableLoading = false;
          } else {
            // 
            showWarningMessage(res.message);
          }
        } else {
          paramMore.conditionJson1 = this.searchData.conditionArr1;
          paramMore.operator = this.searchData.groupOperator;
          paramMore.conditionJson2 = this.searchData.conditionArr2;
          const res = await materialsGetPageMaterialsByConditionAction(params, paramMore);
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // 
            this.tableDatas = res.rows;
            this.tablePage.total = res.total;
            this.tableLoading = false;
          } else {
            // 
            showWarningMessage(res.message);
          }
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param row 
     * @return: 
     * @author: ZJM
     */
    setUpdate(row) {
      this.dialogVisibleUpdate = true;
      this.getGroupTree();
      this.getTypeTree();
      this.formUpdate = JSON.parse(JSON.stringify(row));
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    async getTree() {
      // 
      try {
        const res = await materialsTypeGetTreeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (res.rows) {
            this.nodeData = res.rows;
          } else {
            this.nodeData = [];
          }
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param row 
     * @return: 
     * @author: ZJM
     */
    toInfo(row) {
      this.getMaterialTree();
      this.getBasicInfo();
      this.formUpdate = { ...row };
      this.formUpdate.materialsTypeId = this.$options.filters.getCascaderIds(row.materialsTypeId, this.typeOptionsY);
      this.dialogVisibleUpdate = true;
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    async getBasicInfo() {
      // 
      try {
        const res = await basicInformationGetListByBasicTypeAction({
          basicInformationEnum: 'MATERIALS_ATTRIBUTES'
        });
        if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
          this.materialsAttributes = res.rows;
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
      try {
        const res = await basicInformationGetListByBasicTypeAction({
          basicInformationEnum: 'MATERIALS_UNIT'
        });
        if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
          this.materialsUnitList = res.rows;
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    async getMaterialTree() {
      // 
      // 
      try {
        const res = await materialTypeGetTreeFunc();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.typeOptions = res.rows;
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
      try {
        const res = await materialsTypeGetTreeAction();
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.typeOptionsY = res.rows;
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param row 
     * @return: 
     * @author: ZJM
     */
    setDelete(row) {
      // 
      this.$confirm(MSG.CONFIRM_DELETE, TITLE.PROMPT, {
        confirmButtonText: BTN_TEXT.CONFIRM,
        cancelButtonText: BTN_TEXT.CANCEL,
        type: 'warning'
      })
        .then(() => {
          this.setDeleteChecks([row.materialsId]);
        })
        .catch(() => { });
    },
    /**
     * @description: 
     * @param name name
     * @return:
     * @author: ZJM
     */
    async togglePage(name) {
      // 
      if (name === 'addDevice') {
        // 
        this.showPage = 1;
      }
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    submitUpdate() {
      this.$refs.updateForm.validate(valid => {
        // 
        if (valid) {
          this.submitUpdateFunc();
        }
      });
    },
    async submitUpdateFunc() {
      try {
        const form = {};
        form.materialsId = this.formUpdate.materialsId;
        form.materialsName = this.formUpdate.materialsName;
        form.materialsModel = this.formUpdate.materialsModel;
        form.materialsAttributesId = this.formUpdate.materialsAttributesId;
        form.materialsUnitId = this.formUpdate.materialsUnitId;
        form.materialsTypeId = this.formUpdate.materialsTypeId[this.formUpdate.materialsTypeId.length - 1];
        const res = await materialsUpdateByIdAction(form);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 
          showSuccessMessage(res.message);
          this.dialogVisibleUpdate = false;
          this.findList();
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 
     * @param val 0index
     * @return:
     * @author: ZJM
     */
    goBack(val) {
      // 
      if (val === 3) {
        this.showPage = 0;
        this.getTree();
      } else {
        this.showPage = val;
        // 
        this.findList();
      }
    },
    print(val) {
      this.$print(this.$refs.printQr);
    },
    /**
     * @description: 
     * @param 
     * @return: 
     * @author: ZJM
     */
    groupConfig() {
      this.showPage = 3;
    },
    /**
     * @description: 
     * @return: void
     * @author: ZJM
     */
    async setTableColume() {
      // try {
      //   const res = await extendPropertyGetListAction(
      //     {},
      //     {
      //       conditionJson1: [
      //         {
      //           column: 'extendType',
      //           conditionJson1: [],
      //           conditionJson2: [],
      //           current: 0,
      //           operator: 1,
      //           operatorGroup: 0,
      //           orderBy: {},
      //           ptype: 'int',
      //           size: 0,
      //           type: 'eq',
      //           value: 1
      //         }
      //       ]
      //     });
      //   if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
      //     this.extendPropertyVoList = res.rows;
      this.tableColumns = [];
      this.tableColumns_head = this.tableColumns_old.slice(0, this.tableColumns_old.length - 1);
      this.tableColumns_operation = this.tableColumns_old[this.tableColumns_old.length - 1];
      this.tableColumns = this.tableColumns.concat(this.tableColumns_head);
      // if (this.extendPropertyVoList.length > 0) {
      //   const arr = this.extendPropertyVoList;
      //   for (var i = 0; i < arr.length; i++) {
      //     this.tableColumns.push({
      //       field: arr[i].propertyCode,
      //       title: arr[i].extendName,
      //       width: '160',
      //       align: 'center',
      //       formatter: this.$options.filters.formatterExtendValue
      //     });
      //   }
      // }
      this.tableColumns = this.tableColumns.concat(this.tableColumns_operation);
      //     // 
      //     for (var x = 0; x < this.tableColumns.length; x++) {
      //       this.tableColumns[x].zIndex = x;
      //       if (this.tableColumns[x].visible === undefined) {
      //         this.tableColumns[x].yVisible = true;
      //       } else {
      //         this.tableColumns[x].yVisible = this.tableColumns[x].visible;
      //       }
      //       if (this.tableColumns[x].width === undefined) {
      //         this.tableColumns[x].yWidth = 'auto';
      //       } else {
      //         this.tableColumns[x].yWidth = this.tableColumns[x].width;
      //       }
      //     }
      //   } else {
      //     // 
      //     showWarningMessage(res.message);
      //   }
      // } catch (error) {
      //   showErrorNotify(TITLE.NOTIFY, error);
      // } finally {
      // }
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      this.onTablePageChange(1, this.tablePage.pageSize);
    },
    /**
     * @description:
     * @param
     * @return:
     * @author: CH
     */
    async GetListQueryParameter() {
      try {
        const res = await materialsGetListMaterialsQueryParameterAction({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.srearchList = res.rows;
        } else {
          // 
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
    * @description: 
    * @param 
    * @return: void
    * @author: cyb
    */
    showSearchCard() {
      this.isSearchBoxShow = !this.isSearchBoxShow;
      if (!this.isSearchBoxShow) {
        this.onTablePageChange(1, this.tablePage.pageSize);
      }
    },
    /**
    * @description: 
    * @param 
    * @return: void
    * @author: ZJM
    */
    showCustomColumn() {
      this.dialogVisibleCustomColumn = !this.dialogVisibleCustomColumn;
      if (!this.dialogVisibleCustomColumn) {
        this.tableColumns = this.tableColumns.sort((a, b) => {
          return a.zIndex > b.zIndex ? 1 : -1;
        });
        this.tableColumns = this.tableColumns.map((item) => {
          item.width = item.yWidth;
          item.visible = item.yVisible;
          return item;
        });
      }
    },
    /**
    * @description: 
    * @param 
    * @return: void
    * @author: ZJM
    */
    saveCustomColumn(columns) {
      this.tableColumns = columns;
      this.showCustomColumn();
    },
    /**
    * @description: 
    * @param 
    * @return: void
    * @author: ZJM
    */
    resetCustomColumn() {
      this.showCustomColumn();
      // 
      this.tableColumns = this.tableColumns.sort((a, b) => {
        return a.zIndex > b.zIndex ? 1 : -1;
      });
      this.tableColumns = this.tableColumns.map((item) => {
        item.width = item.yWidth;
        item.visible = item.yVisible;
        return item;
      });
    },
    refreshTable() {
      this.findList();
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: ZJM
     */
    importTable() {
      this.isImportDialogShow = true;
      setTimeout(() => {
        this.$refs.ImportDialog.isShow = true;
        this.$refs.ImportDialog.resetUploadProgressBar();
      }, 0);
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTemplate() {
      try {
        const res = await exportExportMaterialsTemplateAction();
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(res, EXPORT_NAME.MATERIAL_TEMPLATE);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:
     * @param {type}
     * @return:
     * @author: ZJM
     */
    submitImportFile(param) {
      const fileObj = param.file;
      const fd = new FormData();
      fd.append('file', fileObj);
      // 
      exportImportMaterialsAction({}, fd, {
        onUploadProgress: event => {
          // eslint-disable-next-line prefer-const
          let uploadedPercentage = Math.floor(
            (event.loaded * 100) / event.total
          );
          // option.onProgress({ percent: uploadedPercentage });
          this.$refs.ImportDialog.uploadPercent = uploadedPercentage;
        }
      })
        .then(res => {
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            param.onSuccess(res);
          } else {
            param.onError(res);
          }
        })
        .catch(err => {
          showWarningMessage(err);
          param.onError(err);
        });
    },
    closeImportDialog() {
      this.findList();
      this.isImportDialogShow = false;
    }
  }
};
