<template>
  <div class="content">
    <tree :nodes="nodes" :setting="setting" @onCreated="handleCreated"></tree>
  </div>
</template>

<script>
// 
import tree from 'vue-giant-tree';
export default {
  components: {
    // 
    tree
  },
  props: {
    // 
    isCheck: {
      type: Boolean,
      default: false
    },
    // 
    isExpendAll: {
      type: Boolean,
      default: false
    },
    // 
    isOneExpendAll: {
      type: Boolean,
      default: false
    },
    // 
    nodes: {
      type: Array,
      default: function() {
        return [{}];
      }
    }
  },
  data() {
    return {
      selectedNode: null,
      // ztree
      ztreeObj: null,
      // ztree
      setting: {
        // async: {
        //   enable: true,
        //   url: this.getUrl, // 
        //   type: 'GET'
        // },
        check: {
          enable: this.isCheck
        },
        data: {
          simpleData: {
            enable: true,
            idKey: 'id',
            pIdKey: 'pId',
            rootPId: '0'
          }
        },
        callback: {
          onClick: this.onClick, // ,
          onCheck: this.onCheck, // 
          onRightClick: this.onRightClick // 
        }
      }
    };
  },
  computed: {},
  created: function() {},
  methods: {
    /**
     * @description: 
     * @param  treeId ztreeid
     * @param  treeNode 
     * @return: 
     * @author: ZJM
     */
    // getUrl: function(treeId, treeNode) {
    //   // 
    //   this.$emit('getZTreeUrl', treeId, treeNode, this.ztreeObj);
    // },
    /**
     * @description: 
     * @param  evt 
     * @param  treeId ztreeid
     * @param  treeNode 
     * @return: 
     * @author: ZJM
     */
    onClick: function(evt, treeId, treeNode) {
      // 
      this.selectedNode = treeNode;
      this.$emit('ztreeOnClick', evt, treeNode, this.ztreeObj);
    },
    /**
     * @description: 
     * @param  evt 
     * @param  treeId ztreeid
     * @param  treeNode 
     * @return: 
     * @author: ZJM
     */
    onCheck: function(evt, treeId, treeNode) {
      // 
      // this.selectedNode = treeNode;
      this.$emit('ztreeOnCheck', evt, treeNode, this.ztreeObj);
    },
    /**
     * @description: 
     * @param  ztreeObj ztree
     * @return: 
     * @author: ZJM
     */
    handleCreated: function(ztreeObj) {
      // ztree
      this.ztreeObj = ztreeObj;
      // 
      if (this.isExpendAll) {
        ztreeObj.expandAll(true);
      } else if (this.isOneExpendAll) {
        /*  */
        var nodes = ztreeObj.transformToArray(ztreeObj.getNodes());
        // 
        ztreeObj.expandNode(nodes[0], true);
      }

      // 
      this.$emit('ztreeHandleCreated', ztreeObj);
    },
    // onCheck: function(evt, treeId, treeNode) {
    // },
    // filter: function(treeId, parentNode, childNodes) {
    // },
    // zTreeOnAsyncSuccess: function(event, treeId, treeNode, msg) {
    // },
    onRightClick(evt, treeId, treeNode) {
      var node = {};
      if (treeNode === null) {
        node = this.selectedNode;
      } else {
        this.selectedNode = treeNode;
        node = treeNode;
      }
      this.ztreeObj.selectNode(node);
      this.$emit('ztreeOnRightClick', evt, node, this.ztreeObj);
    },
    //  JSON 
    getNodesByParam(key, value, parentNode) {
      this.ztreeObj.expandAll(true);
      // return this.ztreeObj.getNodesByParam(key, value, parentNode);
    },
    // 
    selectNode(treeNode, addFlag, isSilent) {
      this.ztreeObj.selectNode(treeNode, addFlag, isSilent);
    }
  }
};
</script>

<style lang="less"  scoped>
* {
  box-sizing: border-box;
}
.ztree {
  width: 100%;
  height: 100%;
  overflow: auto;
}
.content {
  width: 100%;
  height: calc(100% - 40px);
  background: #fff;
  padding: 10px;
}
.ztree /deep/ li {
  line-height: 0 !important;
}
.ztree /deep/ li span.button.yes_ico_docu {
  margin-right: 2px;
  background-image: url('~@/assets/img/DeviceCheck/yes.png') !important;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  vertical-align: top;
  *vertical-align: middle;
  width: 15px !important;
  height: 15px !important;
  vertical-align: text-top;
}
.ztree /deep/ li span.button.no_ico_docu {
  margin-right: 2px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-image: url('~@/assets/img/DeviceCheck/no.png') !important;
  vertical-align: top;
  *vertical-align: middle;
  width: 15px !important;
  height: 15px !important;
  vertical-align: text-top;
}
</style>
