      //         editRender: { name: 'input' }
      //       });
      //     } else if (row[i].propertyType === 2) {
      //       this.tableColumns.push({
      //         field: row[i].propertyCode,
      //         title: row[i].extendName,
      //         width: '160',
      //         align: 'center',
      //         className: i,
      //         editRender: { name: '$input', props: { type: 'number' } }
      //       });
      //     } else if (row[i].propertyType === 3) {
      //       var num = '0';
      //       if (row[i].propertyLength === 1) {
      //         num = '220';
      //       } else if (row[i].propertyLength === 2) {
      //         num = '160';
      //       } else if (row[i].propertyLength === 3) {
      //         num = '160';
      //       }
      //       this.tableColumns.push({
      //         field: row[i].propertyCode,
      //         title: row[i].extendName,
      //         width: num,
      //         align: 'center',
      //         className: i,
      //         editRender: { name: 'ElDatePicker', props: { type: 'datetime', format: FORM_CONFIG_DATE[row[i].propertyLength] } }
      //       });
      //     }
